<template>
  <div class="wheel" :class="wheelClass">
    <img class="wheel-img" :style="wheelStyle" :src="wheelImage">
    <img class="point-img" :src="pointImage" @click.stop="clickPoint">
  </div>
</template>

<script>
//  @author zzt
export default {
  name: 'GiftWheel',
  props: {
    // 
    wheelClass: {
      type: String,
      default: ''
    },
    // 
    wheelImage: {
      type: String,
      default: ''
    },
    // 
    pointImage: {
      type: String,
      default: ''
    },
    // 
    rotating: {
      type: Boolean,
      default: false
    },
    // 
    turns: {
      type: Number,
      default: 10
    },
    // 
    level: {
      type: [String, Number],
      default: ''
    },
    // 
    levelList: {
      type: Array,
      default: () => []
    },
    // 
    rotateTime: {
      type: Number,
      default: 5
    },
    // 
    reset: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      wheelStyle: {
        transition: '',
        transform: 'rotate(0deg)'
      },
      rotateDeg: 0
    }
  },
  watch: {
    // 
    rotating: {
      handler(isRotating) {
        if (isRotating) this.rotateWheel()
      }
    },
    reset: {
      handler(isReset) {
        if (isReset) this.resetWheel()
      }
    }
  },
  methods: {
    // 
    calcRotateDeg() {
      const OneTurnDeg = 360
      // level
      const levelIndex = this.levelList.indexOf(this.level)
      if (levelIndex > -1) {
        // 
        const restDeg = OneTurnDeg - (OneTurnDeg / this.levelList.length) * (levelIndex + 0.5)
        this.rotateDeg = restDeg + OneTurnDeg * this.turns
      } else {
        throw new Error('current level is not in levelList')
      }
    },
    // 
    rotateWheel() {
      this.calcRotateDeg()
      this.wheelStyle.transition = `transform ${this.rotateTime}s ease`
      this.$nextTick(() => {
        this.$emit('start-rotating')
        this.wheelStyle.transform = `rotate(${this.rotateDeg}deg)`
        this.$nextTick(() => {
          setTimeout(() => {
            this.$emit('stop-rotating')
          }, this.rotateTime * 1000)
        })
      })
    },
    // 
    resetWheel() {
      this.wheelStyle.transition = ''
      this.wheelStyle.transform = 'rotate(0deg)'
    },
    clickPoint() {
      this.$emit('click-point')
    }
  }
}

</script>

<style scoped lang="scss">
.wheel {
    position: relative;
    width: 100%;
    height: 100%;

    .wheel-img {
        width: 100%;
        height: 100%;
    }

    .point-img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 2.026667rem;
        height: 2.533333rem;
    }
}
</style>
